.mk_btn {
  height: 1.6rem;
  transition: all 0.1s cubic-bezier(0.65, 0.05, 0.36, 1);
  border: none;
  border-radius: 0rem;
  background: initial;
  border: 0.05rem solid #ddd;
  outline: none;
  min-width: 3.9rem;
  cursor: pointer;
  transition: all 0.5s;
}
.mk_btn:hover {
  border-color: rgba(6, 147, 227, 0.8);
}
.mk_btn.btn_circle {
  border-radius: 50%;
  padding: 0;
  width: 1.75rem;
  min-width: initial;
  background: #0693e3;
  color: #fff;
  fill: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 0;
}
.mk_btn.btn_error {
  color: #fa0000 !important;
  border-color: #fa0000;
  background: #ffe4e8;
}
.mk_btn.btn_primary {
  min-width: 3.9rem;
  background: #0693e3;
  color: #fff;
  border-color: #0693e3;
  align-items: center;
  cursor: pointer;
}
.mk_btn.btn_primary:hover {
  border-color: rgba(6, 147, 227, 0.8);
  background: rgba(6, 147, 227, 0.8);
}
.mk_btn.fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-radius: 0;
  border: none;
  background: #0693e3;
}
.mk_btn.disabled {
  border-color: #d9d9d9 !important;
  color: #d9d9d9 !important;
  cursor: no-drop;
}
.mk_btn_loading {
  vertical-align: middle;
  position: relative;
  fill: #fff;
  top: -0.1rem;
  right: 0.25rem;
}
