.mk_alert {
  padding: 0.75rem;
}
.mk_alert_info {
  border: 0.05rem solid rgba(24, 144, 255, 0.4);
  background: rgba(24, 144, 255, 0.2);
}
.mk_alert_info.inherit_color {
  color: #1890ff;
}
.mk_alert_success {
  border: 0.05rem solid rgba(82, 196, 26, 0.4);
  background: rgba(82, 196, 26, 0.2);
}
.mk_alert_success.inherit_color {
  color: #52c41a;
}
.mk_alert_warning {
  border: 0.05rem solid rgba(250, 173, 20, 0.4);
  background: rgba(250, 173, 20, 0.2);
}
.mk_alert_warning.inherit_color {
  color: #faad14;
}
.mk_alert_error {
  border: 0.05rem solid rgba(245, 34, 45, 0.4);
  background: rgba(245, 34, 45, 0.2);
}
.mk_alert_error.inherit_color {
  color: #f5222d;
}
.mk_alert_title {
  margin-bottom: 0.3rem;
}
