.mk_colors {
  position: relative;
  height: 1.5rem;
}
.mk_colors_color {
  width: 4rem;
  height: 1.5rem;
  padding: 0.2rem;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0 0 0 0.05rem;
  cursor: pointer;
}
.mk_colors_color_box {
  width: 100%;
  height: 100%;
}
.mk_colors_mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9;
}
.mk_colors_select {
  position: absolute;
  top: 2rem;
}
.mk_colors_label {
  margin-left: 0.4rem;
  font-size: 0.7rem;
}
.mk_colors_sketch {
  height: 15rem;
}
.mk_colors_chrome {
  height: 12rem;
}
.mk_colors_block {
  height: 11rem;
}
.mk_colors_github {
  height: 3rem;
}
