.mk_upload_dragger {
  width: 100%;
}
.mk_upload_dragger__box {
  height: 8.6rem;
  border-radius: 0rem;
  border: 0.05rem dashed #d9d9d9;
  background: #fafafa;
  position: relative;
  cursor: pointer;
  transition: all 0.5s;
  padding: 0.8rem 0;
}
.mk_upload_dragger__box:hover {
  border-color: #0693e3;
}
.mk_upload_dragger__box_default {
  text-align: center;
}
.mk_upload_dragger__box_default__icon {
  margin: 0 0 1rem;
}
.mk_upload_dragger__box_default__title {
  line-height: 1.5;
  margin: 0 0 0.2rem;
  color: rgba(0, 0, 0, 0.85);
  font-size: 0.8rem;
}
.mk_upload_dragger__box_default__label {
  color: rgba(0, 0, 0, 0.45);
  font-size: 0.7rem;
  height: 2rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.mk_upload_dragger_upload__view {
  margin-top: 0.5rem;
}
.mk_upload_dragger_upload__view__item {
  margin-top: 0.25rem;
}
.mk_upload_dragger_upload__view__item:first-child {
  margin-top: 0;
}
.mk_upload_dragger_upload__view__item__icon {
  width: 2rem;
  height: 2rem;
  border: 0.05rem solid #d9d9d9;
}
.mk_upload_dragger_upload__view__item__progress {
  padding-left: 0.5rem;
}
