.mk_nav_bar {
  background: #0693e3;
  height: 2rem;
  padding: 0 0.7rem;
  box-sizing: border-box;
  z-index: 8;
}
.mk_nav_bar.fixed {
  top: 0;
}
.mk_nav_bar_left {
  padding-right: 0.5rem;
  min-width: 1.4rem;
}
.mk_nav_bar_title {
  font-size: 0.7rem;
}
.mk_nav_bar_right {
  padding-left: 0.5rem;
  min-width: 1.4rem;
}
.mk_nav_bar_right__img {
  width: 1.1rem;
  height: 1.1rem;
}
