.mk_input {
  height: 1.6rem;
  font-size: 0.6rem;
  position: relative;
}
.mk_input__label {
  width: 3rem;
}
.mk_input.active {
  height: 2.5rem;
}
.mk_input.active input {
  top: 0.9rem;
  height: calc(100% - 0.9rem);
}
.mk_input_text {
  height: 0.9rem;
  font-size: 0.6rem;
  position: absolute;
  transform: translate(0, 0.95rem);
  color: #888888;
  pointer-events: none;
  z-index: 1;
  transition: all 0.5s;
}
.mk_input_text.active {
  transform: translate(0, 0);
  color: #333333;
}
.mk_input_box {
  position: relative;
}
.mk_input_box input {
  padding: 0 0.5rem;
  outline: none;
  height: 100%;
  width: 100%;
  position: relative;
  font-size: 0.6rem;
  border: 0.05rem solid #e8e8e8;
  transition: all 0.5s;
  -moz-appearance: textfield;
  /* 使用webkit内核的浏览器 */
  /* Firefox版本4-18 */
  /* Firefox版本19+ */
  /* IE浏览器 */
}
.mk_input_box input::-webkit-inner-spin-button,
.mk_input_box input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}
.mk_input_box input::-webkit-input-placeholder {
  font-size: 0.6rem;
}
.mk_input_box input:-moz-placeholder {
  font-size: 0.6rem;
}
.mk_input_box input::-moz-placeholder {
  font-size: 0.6rem;
}
.mk_input_box input:-ms-input-placeholder {
  font-size: 0.6rem;
}
.mk_input_box input:focus {
  border-color: #0693e3;
}
.mk_input_box input:hover {
  border-color: #0693e3 !important;
}
.mk_input_box input:disabled {
  background: #fff;
}
.mk_input_box input:disabled:hover {
  border-color: #e8e8e8 !important;
  cursor: not-allowed;
}
.mk_input_close_icon {
  transition: all 0.5s;
  position: absolute;
  right: 0.5rem;
  top: 50%;
  transform: translate(0, -50%);
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}
.mk_input_close_icon * {
  color: #fff;
  fill: #fff;
}
.mk_input_max_length {
  transition: all 0.5s;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: 0.5rem;
}
.mk_input_extend {
  padding: 0 0.5rem;
}
.mk_input.mk_input_search {
  min-width: 16rem;
}
.mk_input.mk_input_search .mk_input_extend {
  padding: 0;
}
.mk_input.mk_input_search .mk_input_search_btn {
  min-width: initial;
  padding: 0 1rem;
}
