.mk_textarea {
  position: relative;
}
.mk_textarea textarea {
  padding: 0.25rem;
  width: 100%;
  border: none;
  outline: none;
  min-height: 4rem;
  font-size: 0.6rem;
  resize: none;
  border: 0.05rem solid #e8e8e8;
  transition: all 0.5s;
}
.mk_textarea textarea:disabled {
  background: transparent;
}
.mk_textarea textarea:disabled:hover {
  border-color: #e8e8e8 !important;
}
.mk_textarea textarea:focus {
  border-color: #0693e3;
}
.mk_textarea textarea:hover {
  border-color: #0693e3 !important;
}
.mk_textarea_max_length {
  position: absolute;
  right: 0.5rem;
  bottom: 0.5rem;
}
