.mk_item {
  min-height: 2rem;
  background: #fff;
  padding: 0 0.6rem;
  transition: all 0.1s cubic-bezier(0.65, 0.05, 0.36, 1);
  position: relative;
  overflow: hidden;
}
.mk_item::after {
  content: "";
  position: absolute;
  height: 0.05rem;
  width: 100%;
  bottom: 0;
  transform: scaleY(0.5);
}
.mk_item.line_solid::after {
  border-bottom: 0.05rem solid #e6e6e6;
}
.mk_item.line_dashed::after {
  border-bottom: 0.05rem dashed #e6e6e6;
}
.mk_item:last-child::after {
  border-bottom: 0;
}
.mk_item.active {
  background-color: #e6e6e6 !important;
}
.mk_item_title {
  color: #333333;
  width: 0;
  text-align: left;
}
.mk_item_right {
  color: #888888;
  font-size: 0.5rem;
}
.mk_item_link {
  margin-left: 0.3rem;
}
.mk_item_link__icon {
  color: #888888;
}
