.mk_progress_inter {
  position: relative;
  height: 0.25rem;
  background: #f5f5f5;
  border-radius: 0rem;
  overflow: hidden;
  transition: all 0.5s;
}
.mk_progress_inter__bg {
  background: #0693e3;
  height: 100%;
  transition: all 0.5s;
  position: absolute;
  left: 0;
  top: 0;
}
.mk_progress_inter_success__bg {
  background: #52c41a;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.5s;
}
.mk_progress__text {
  display: inline-block;
  margin-left: 0.4rem;
  color: rgba(0, 0, 0, 0.45);
  line-height: 1;
  white-space: nowrap;
  text-align: left;
  vertical-align: middle;
  word-break: normal;
}
