.nav_color {
  background: transparent;
}
.component_list {
  min-height: 2rem;
}
.component_label {
  display: flex;
  align-items: flex-start;
  margin-right: 0.3rem;
}
.icons_items {
  cursor: pointer;
  transition: all 0.5s;
}
.icons_items:hover {
  box-shadow: 0 0 0.5rem #ccc;
}
.image {
  display: inline-block;
  width: 9.1rem;
  height: 9.1rem;
  margin: 0.5rem;
  overflow: hidden;
  background: #d9d9d9;
  cursor: pointer;
}
.image_item {
  width: 100%;
  height: auto;
}
.uploadViewClassName {
  height: 6rem;
  overflow: auto;
}
.scroll_view {
  height: calc(100% - 2rem - 2rem);
}
