.mk_menu {
  background-color: #fff;
  width: 8rem;
  overflow: hidden;
  transition: all 0.2s;
  margin: 0;
}
.mk_menu.fold {
  width: 2.6rem;
}
.mk_menu_item {
  height: 2.25rem;
  line-height: 2.25rem;
  color: rgba(51, 51, 51, 0.7);
  position: relative;
}
.mk_menu_item a {
  color: inherit;
  text-decoration: none;
}
.mk_menu_item::after {
  content: '';
  height: 100%;
  left: 0;
  width: 0;
  top: 0;
  position: absolute;
  background-color: #0693e3;
}
.mk_menu_item::before {
  content: '';
  height: 100%;
  width: 0%;
  transition: all 0.5s;
  position: absolute;
}
.mk_menu_item.active {
  color: #333;
}
.mk_menu_item.active::after {
  width: 0.1rem;
}
.mk_menu_item.active::before {
  width: 100%;
  background: rgba(6, 147, 227, 0.2);
}
.mk_menu_item.active .mk_menu_item__label_icon {
  color: #0693e3;
}
.mk_menu_item__label {
  height: 100%;
  padding: 0 0.45rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
  width: 100%;
  white-space: nowrap;
}
.mk_menu_item__label.single {
  padding-left: 1rem;
}
.mk_menu_item__label:hover {
  transition: all 0.5s;
  color: #333;
}
.mk_menu_item__label_icon {
  padding: 0.3rem;
}
.mk_menu_item__label_title {
  padding-left: 0.3rem;
}
.mk_menu_group {
  min-height: 2.25rem;
  line-height: 2.25rem;
  color: rgba(51, 51, 51, 0.7);
  position: relative;
  cursor: pointer;
}
.mk_menu_group_box {
  min-height: 2.25rem;
}
.mk_menu_group_title__icon {
  padding: 0.3rem;
}
.mk_menu_group_title__label {
  white-space: nowrap;
}
.mk_menu_group_title li:first-child:hover {
  transition: all 0.5s;
  color: #333;
}
.mk_menu_group_content {
  transition: 0.5s all;
  height: 0;
  overflow: hidden;
  opacity: 0;
}
.mk_menu_group_content .mk_menu_item__label {
  padding-left: 0.75rem;
}
.mk_menu_group .active {
  height: 100%;
  opacity: 1;
}
