.mk_image_picker {
  font-size: 0.9rem;
  background: #fff;
}
.mk_image_picker__add {
  height: 4rem;
  width: 4rem;
  border: 0.05rem dashed #e6e6e6;
  border-radius: 0rem;
  cursor: pointer;
  margin-right: 0.5rem;
  margin-bottom: 0.25rem;
  display: inline-block;
  vertical-align: middle;
  position: relative;
}
.mk_image_picker__add_close {
  position: absolute;
  right: 0.25rem;
  top: 0.25rem;
}
.mk_image_picker__add:first-of-type {
  margin-left: 0;
}
.mk_image_picker__add_box {
  height: 100%;
}
.mk_image_picker__add_box.picker_img {
  height: calc(100% - 10*0.05rem);
  width: calc(100% - 10*0.05rem);
  margin: 0.25rem;
  box-sizing: border-box;
  overflow: hidden;
}
.mk_image_picker__add_box_img {
  width: 100%;
}
.mk_image_picker_crop__container {
  background: #fff;
}
