.mk_tab_bar {
  background: #fff;
  transition: all 0.1s cubic-bezier(0.65, 0.05, 0.36, 1);
  position: relative;
  box-sizing: border-box;
  height: 12rem;
}
.mk_tab_bar_vertical {
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.mk_tab_bar_vertical .mk_tab_bar_active_bar {
  transition: all 0.3s;
  width: 0.1rem;
  height: 100%;
  background: #0693e3;
  position: absolute;
  top: 0;
  left: 0;
}
.mk_tab_bar_horizontal {
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.mk_tab_bar_horizontal .mk_tab_bar_active_bar {
  transition: all 0.3s;
  width: 100%;
  height: 0.1rem;
  background: #0693e3;
  position: absolute;
  bottom: 0;
  left: 0;
}
.mk_tab_bar_item {
  padding: 0.3rem 0.5rem;
  height: 2.25rem;
  cursor: pointer;
  flex-shrink: 0;
}
.mk_tab_bar_item_icon {
  height: 1.05rem;
}
.mk_tab_bar_item_icon img {
  height: 100%;
}
.mk_tab_bar_item_label {
  height: 0.9rem;
  font-size: 0.5rem;
}
.mk_tab_bar_item_view {
  transition: all 0.3s;
  padding: 0.5rem 0.75rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.mk_tab_bar_item_view.mk_vertical {
  height: 100%;
  width: 100%;
}
.mk_tab_bar_item_view.mk_horizontal {
  height: 100%;
  width: 100%;
  flex-shrink: 0;
}
.mk_tab_bar_item .all_height {
  height: 100%;
}
.mk_tab_bar_view.mk_vertical {
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.mk_tab_bar_view.mk_horizontal {
  flex: 1;
  width: 100%;
  overflow: hidden;
}
.mk_tab_bar.fixed {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.mk_tab_bar_vertical {
  display: flex;
  flex-direction: column;
  min-width: 2.5rem;
}
.mk_tab_bar.mk_menu {
  height: 100vh;
}
.mk_tab_bar.mk_menu .mk_tab_bar_view > .mk_tab_bar_item_view {
  padding: 0;
}
