.mk_dialog {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9;
  overflow: hidden;
}
.mk_dialog.fadeIn {
  animation: mk_dialog_fadeIn 0.3s forwards;
}
.mk_dialog.fadeOut {
  background: rgba(0, 0, 0, 0.5);
  animation: mk_dialog_fadeOut 0.3s 0.5s forwards;
}
.mk_dialog_content {
  min-width: 30rem;
  max-height: calc(100% - 100 * 0.05rem);
  background: #fff;
  border-radius: 0rem;
  overflow: hidden;
  box-shadow: 0 0 0.5rem #6b6b6b;
}
.mk_dialog_content.slipUp {
  transform: translate3d(0, 100vh, 0);
  animation: mk_dialog_slipToUp 0.5s 0.2s forwards cubic-bezier(0.3, 0.23, 0.22, 1.04);
}
.mk_dialog_content.slipBottom {
  animation: mk_dialog_slipToBottom 0.5s forwards;
}
.mk_dialog_content__navbar {
  background: transparent !important;
}
.mk_dialog_content__navbar .navbar_label {
  font-size: 0.7rem;
}
.mk_dialog_content__navbar:last-child {
  height: 2.5rem !important;
}
.mk_dialog_content__box {
  min-height: 15rem;
  position: relative;
  margin: 0 1rem;
  overflow: auto;
}
@keyframes mk_dialog_fadeIn {
  from {
    background: rgba(0, 0, 0, 0);
  }
  to {
    background: rgba(0, 0, 0, 0.5);
  }
}
@keyframes mk_dialog_fadeOut {
  from {
    background: rgba(0, 0, 0, 0.5);
  }
  to {
    background: rgba(0, 0, 0, 0);
  }
}
@keyframes mk_dialog_slipToUp {
  from {
    transform: translate3d(0, 100vh, 0);
  }
  to {
    transform: translate3d(0, 0vh, 0);
  }
}
@keyframes mk_dialog_slipToBottom {
  from {
    transform: translate3d(0, 0vh, 0);
  }
  to {
    transform: translate3d(0, 100vh, 0);
  }
}
