.home_logo {
  height: 2.5rem;
  background: #1a1c24;
}
.home_collapsed {
  cursor: pointer;
  background: #21232a;
}
.home_collapsed :global .collapsed_icon {
  fill: #7c838a !important;
}
.home_collapsed:hover :global .collapsed_icon {
  fill: #fff !important;
}
.home_nav {
  height: 2.5rem;
  background: #fff;
  padding: 0;
}
.home_nav .nav_left {
  max-width: 20rem;
  flex: 1;
  padding: 0;
}
.home_nav .nav_right {
  padding: 0;
}
.home_nav .nav_user {
  width: 8rem;
}
.home_nav .nav_title {
  position: relative;
}
.home_nav .nav_title::after {
  content: "";
  position: absolute;
  width: 0.05rem;
  height: 100%;
  background: #e6e6e6;
  transform: scaleX(0.5);
  right: 0;
  top: 0;
}
.home_nav_list {
  min-width: 2.5rem;
  cursor: pointer;
  height: 2.5rem;
  position: relative;
}
.home_nav_list .nav_input {
  padding-left: 0.75rem;
  background: #fff;
  height: 2.5rem;
}
.home_nav_list .nav_input input {
  border: none;
}
.home_nav_list .nav_input input:focus {
  border: none;
}
.home_nav_list::after {
  content: "";
  position: absolute;
  width: 0.05rem;
  height: 100%;
  background: #e6e6e6;
  transform: scaleX(0.5);
  right: 0;
  top: 0;
}
.home_nav_list:hover {
  background: #f9f9f9;
}
.home_nav_item {
  cursor: pointer;
  padding: 0 0.75rem;
  height: 100%;
}
.home_nav_item:hover {
  color: #5294e0;
  transition: all 0.5s;
}
.home_nav_item:hover .mk_icon {
  fill: #5294e0;
  transition: all 0.5s;
}
.home_menu {
  background: #1a1c24 !important;
}
.home_menu :global .mk_menu_group_title__label {
  color: white;
}
.home_menu :global .mk_menu_item {
  color: #a8adaf;
}
.home_menu :global .mk_menu_item__label:hover {
  color: white;
}
.home_menu :global .mk_menu_item.active {
  color: white;
}
.home_view {
  height: 100%;
}
.home_scroll {
  padding: 0.5rem 1.25rem;
  overflow: auto;
}
.nav_bar {
  background: #fff;
}
.nav_bar_label {
  font-weight: initial;
}
.page_menu {
  background: #fff;
  position: relative;
}
.page_menu_title {
  width: 5.5rem;
  height: 3rem;
  line-height: 3rem;
  white-space: nowrap;
}
.page_menu_title_solo {
  position: absolute;
  cursor: pointer;
  height: 1.5rem;
  width: 1rem;
  right: 0;
  top: 1.5rem;
  transform: translate(0, -50%);
  background: #f5f7f9;
}
.page_menu_title_solo.show_solo {
  background: #fff;
  right: -1rem;
  transition: all 0.4s;
}
.animate_menu {
  width: 5.5rem;
  transition: all 0.4s;
  overflow: hidden;
}
.fold {
  width: 0;
}
