.app_view {
  margin-top: 0.5rem;
  background: #fff;
  padding: 1rem 1.5rem;
  padding-bottom: 3rem;
}
.app_view_tab_item {
  width: 4rem;
}
.app_view_dialog {
  width: 28.5rem;
  min-width: initial;
}
.app_view_diy {
  width: 5.2rem;
  margin: 1.5rem 0.7rem;
  margin-top: 0;
  text-align: center;
  padding: 0.8rem 0;
  border-radius: 0.2rem;
  display: inline-block;
}
.app_view_diy p {
  line-height: 2.2rem;
  font-size: 0.7rem;
  margin-bottom: 0.6rem;
}
.app_view_diy:nth-child(1) {
  background: #e9fae4;
  color: #7ecf6b;
}
.app_view_diy:nth-child(1) :global .mk_btn {
  border-color: #7ecf6b !important;
}
.app_view_diy:nth-child(1) :global .mk_btn:hover {
  color: #48a034 !important;
  border-color: #48a034 !important;
}
.app_view_diy:nth-child(2) {
  background: #fffbdf;
  color: #ffb243;
}
.app_view_diy:nth-child(2) :global .mk_btn {
  border-color: #ffb243 !important;
}
.app_view_diy:nth-child(2) :global .mk_btn:hover {
  color: #dc8200 !important;
  border-color: #dc8200 !important;
}
.app_view_diy:nth-child(3) {
  background: #fff3f3;
  color: #ff5555;
}
.app_view_diy:nth-child(3) :global .mk_btn {
  border-color: #ff5555 !important;
}
.app_view_diy:nth-child(3) :global .mk_btn:hover {
  color: #ee0000 !important;
  border-color: #ee0000 !important;
}
.app_view_diy:nth-child(4) {
  background: #edf6ff;
  color: #5cacf6;
}
.app_view_diy:nth-child(4) :global .mk_btn {
  border-color: #5cacf6 !important;
}
.app_view_diy:nth-child(4) :global .mk_btn:hover {
  color: #0c7ae0 !important;
  border-color: #0c7ae0 !important;
}
.app_view_diy:nth-child(5) {
  background: #fef1e3;
  color: #ff8a33;
}
.app_view_diy:nth-child(5) :global .mk_btn {
  border-color: #ff8a33 !important;
}
.app_view_diy:nth-child(5) :global .mk_btn:hover {
  color: #cc5700 !important;
  border-color: #cc5700 !important;
}
.app_view_diy:nth-child(6) {
  background: url('/static/coming.png');
  height: 5.8rem;
  background-size: 100% 100%;
  vertical-align: top;
}
.app_view_list {
  border: solid 0.05rem #eaeaea;
  box-shadow: 0 0 0.85rem rgba(0, 0, 0, 0);
  margin: 0 1rem 0 0;
  width: 12rem;
  margin-right: 0.75rem;
  flex-shrink: 0;
}
.app_view_list_name {
  height: 4.75rem;
  padding: 2.05rem 1.5rem 0;
  font-size: 0.9rem;
  line-height: 2.7rem;
  color: #000;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.app_view_list_qrcode {
  height: 5.7rem;
  width: 5.7rem;
  background: #f4f4f4;
  margin: auto;
  position: relative;
  border-radius: 0.5rem;
}
.app_view_list_img {
  width: 5.7rem;
  height: 5.7rem;
}
.app_view_list_line {
  height: 0.05rem;
  background: #f0f0ef;
  margin: 1.2rem 1rem 0 1rem;
}
.app_view_list_text {
  padding: 1rem 1.5rem;
}
.app_view_list_btn {
  margin-top: 1rem;
  width: 100%;
}
.form_style {
  padding: 0;
}
.com {
  height: 100%;
  width: 19rem;
}
.com_label {
  margin: 0.25rem 0;
  display: block;
}
.com_bar {
  position: relative;
}
.com_bar::after {
  content: "";
  position: absolute;
  width: 0.05rem;
  height: 100%;
  background: #e6e6e6;
  transform: scaleX(0.5);
  right: 0;
  top: 0;
}
.com_actions {
  height: 2.5rem;
  position: relative;
}
.com_actions_list {
  height: 2.5rem;
  cursor: pointer;
}
.com_actions_list:hover :global .mk_icon {
  transition: all 0.2s;
  fill: #0693e3 !important;
}
.tpl_phone {
  background: #fff;
  border: 0.05rem solid #dcdcdc;
  cursor: move;
  transition: transform 0.5s 0.1s;
  border-radius: 0.75rem;
  height: 27.5rem;
  width: 15.5rem;
  padding: 0 0.25rem 0.25rem 0.25rem;
  overflow: hidden;
}
.tpl_phone .m_tit {
  height: 1.2rem;
  text-align: center;
}
.tpl_phone .m_tit span {
  display: inline-block;
  margin-top: 0.6rem;
  background: #cfcfcf;
}
.tpl_phone .m_tit .cri {
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 100%;
  margin-right: 0.5rem;
}
.tpl_phone .m_tit .lon {
  width: 2.4rem;
  height: 0.2rem;
  border-radius: 0.35rem;
}
.tpl_phone .m_scroll_view {
  border: 0.05rem solid #dcdcdc;
  border-radius: 0.25rem;
  overflow: auto;
}
.drag_box {
  height: 4rem;
}
.drag_box.show_line {
  border: 0.05rem dashed #333;
}
.edit_view {
  position: relative;
}
.edit_view_over {
  border: 0.05rem dashed #333;
}
.edit_view_box {
  position: absolute;
  z-index: 8;
  right: 1rem;
  bottom: 0;
}
.edit_view_box_label {
  background: #000000;
  color: #fff;
  margin-right: 0.35rem;
  font-size: 0.6rem;
  padding: 0.15rem 0.25rem;
  cursor: pointer;
}
.edit_view_box_label:last-child {
  margin-right: 0;
}
.label_view {
  height: 400px;
}
.label_view_list {
  border: 0.05rem solid #eee;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  position: relative;
}
.label_view .label_list {
  padding: 0 0.5rem;
  border: 0.05rem solid #efefef;
  border-right: 0;
}
.label_view .label_list_btn {
  height: 1.5rem;
}
.label_view .label_list_view {
  width: 3.4rem;
  height: 3.4rem;
  margin-right: 0.5rem;
  background: #d8d8d8;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.label_view .label_list_view_bot {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
}
.label_view .label_list_int {
  height: 1.5rem;
}
.label_view .label_list_icon {
  position: absolute;
  right: 0;
  top: 0;
  transform: translate(50%, -50%);
}
