.mk_pagination {
  display: inline-block;
  height: 2rem;
  padding-top: 0.2rem;
}
.mk_pagination_btn {
  width: 1.6rem;
  min-width: initial;
  margin-left: 0.25rem;
  padding: 0;
}
.mk_pagination_btn:first-child {
  margin-left: 0;
}
.mk_pagination_btn.mk_select {
  border-color: #0693e3;
}
.mk_pagination_btn.mk_notline {
  border: 0;
}
