.mk_label-header {
  height: 1.75rem;
  padding-left: 1rem;
}
.mk_label-header__title {
  position: relative;
  font-size: 0.65rem;
  font-weight: 800;
  color: #3d3d3d;
}
.mk_label-header__title.vertical {
  padding-left: 0.5rem;
}
.mk_label-header__title.horizontal::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.2rem;
  background: #0693e3;
  bottom: 0;
  left: 0;
}
.mk_label-header__title.vertical::after {
  content: "";
  position: absolute;
  width: 0.2rem;
  height: 100%;
  left: 0;
  top: 0;
  background: #0693e3;
}
