.mk_l_form {
  padding: 1rem;
}
.mk_l_form__list {
  min-height: 2rem;
}
.mk_l_form.column .mk_l_form__list {
  margin-top: 0.25rem;
}
.mk_l_form.column .mk_l_form__list:first-child {
  margin-top: 0;
}
.mk_l_form.column .mk_l_form__list_label {
  display: flex;
  align-items: flex-start;
  margin-right: 0.3rem;
}
.mk_l_form_upload_label {
  display: flex;
  align-items: flex-end;
  padding-bottom: 0.5rem;
  margin-right: 0.3rem;
}
.mk_l_form_radio_group {
  height: 2.5rem;
}
.mk_l_form_radio_group .ant-radio-wrapper {
  line-height: 2.5rem;
}
.mk_l_form_radio_group_label {
  padding-right: 0.5rem;
}
.mk_l_form_label {
  height: 2.25rem;
}
.mk_l_form__additional {
  display: flex;
  padding-top: 0.5rem;
  margin-top: 0.5rem;
  border-top: 0.05rem dashed #eee;
}
