.mk_search_bar__title {
  background: #f4f4f4;
}
.mk_search_bar_search {
  position: relative;
}
.mk_search_bar_search__int {
  height: 1.2rem;
  border-radius: 0.7rem;
  overflow: hidden;
}
.mk_search_bar_search__int input {
  border: 0;
}
.mk_search_bar_search .mk_btn_close {
  margin-left: 0.2rem;
  position: absolute;
  height: 1.3rem;
  top: 0.1rem;
  border-radius: 0.65rem;
  padding: 0 0.55rem;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: 0.65rem;
}
.mk_search_bar_search .mk_font {
  font-size: 0.6rem !important;
  position: relative;
  left: 0.2rem;
}
